.author__container {
    margin: 0 82px 0 37px;
    padding: 71px 0 104px 0;
    display: flex;
    justify-content: space-between;
}

.author__avatar {
    background: transparent;
    border-radius: 50%;
    cursor: pointer;
    position: relative;
    border: none;
    margin: 0;
    padding: 0;
    color: black;
    width: 229px;
    height: 229px;
    filter: drop-shadow(0px 8px 10px rgba(0, 0, 0, 0.25));
}

.author__avatar:hover {
    opacity: 1;
}

.author__avatar:hover::after {
    background-color: rgba(155, 147, 147, 0.8);
    background-image: url(../../images/AuthorEdit.svg);
    background-position: 50%;
    background-repeat: no-repeat;
    border-radius: 50%;
    bottom: 0;
    content: "";
    cursor: pointer;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    transition: opacity .3s ease-in-out;
}

.author__avatar-image {
    border-radius: 50%;
    margin: 0;
    min-height: 229px;
    object-fit: cover;
    width: 229px;
}

.author__info {
    margin: 0;
    padding: 0;
    width: 765px;
}

.author__data {
    margin: 0;
    padding: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.author__name {
    margin: 0;
    padding: 0;
    border-radius: 28px;
    background: #E0BE99;
    width: 648px;
    min-height: 79px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: #121110;
    font-family: 'DM Serif Text', 'Arial', sans-serif;
    filter: drop-shadow(0px 8px 10px rgba(0, 0, 0, 0.25));
    font-size: 39px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    letter-spacing: 0.39px;
}

.author__name-edit {
    margin: 0;
    padding: 0;
    width: 78px;
    height: 72px;
    filter: drop-shadow(0px 8px 10px rgba(0, 0, 0, 0.25));
    background: #E0BE99;
    border: none;
    border-radius: 28px;
    background-image: url(../../images/AuthorEdit.svg);
    background-repeat: no-repeat;
    background-size: 28px;
    background-position: 50%;
}

.author__name-edit:hover {
    cursor: pointer;
}

.author__works {
    margin: 26px 0 0 53px;
    padding: 0;
    width: 559px;
    height: 96px;
    border-radius: 28px;
    background: #E0BE99;
    border: none;
    color: #000;
    font-family: 'DM Serif Text', 'Arial', sans-serif;
    font-size: 41px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    letter-spacing: 0.41px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    filter: drop-shadow(0px 8px 10px rgba(0, 0, 0, 0.25));
}

.author__sidmaster {
    display: flex;
    width: 100%;
    height: 174px;
    padding: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;
    border-radius: 20px;
    background: #F4F0EC;
}

.author__item {
    list-style: none;
    margin: 0;
    padding: 0;
}

.author__item-list {
    font-family: 'Jost', 'Arial', sans-serif;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    letter-spacing: 0.22px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.author__item-number {
    color: #CDA274;
}

.author__item-name {
    color: #000;
}

.author__model {
    width: 100%;
}

.author__add {
    margin: 70px auto 23px;
    padding: 0;
    display: flex;
    width: 399px;
    height: 49px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;
    border-radius: 18px;
    border: none;
    background: #E0BE99;
    box-shadow: 0px 10px 20px 0px rgba(192, 192, 192, 0.35);
    color: #121110;
    text-align: center;
    font-family: Jost;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 125%;
    letter-spacing: 0.36px;
}

.author__add:hover {
    cursor: pointer;
    background: rgba(107, 89, 69, 0.60);
}

.author__icons {
    margin: 0 auto;
    padding: 36px 0 0;
    width: 570px;
    height: 110px;
    border-radius: 65px 0px;
    background: #E0BE99;
    display: flex;
    justify-content: space-evenly;
}

.author__icon {
    display: flex;
    align-items: center;
    gap: 24px;
}

.author__like {
    margin: 0;
    padding: 0;
}

.author__text {
    margin: 0;
    padding: 0;
}

.author__coments {
    margin: 0;
    padding: 0;
}

.author__icon-number {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 14px;
}

.author__icon-press:hover {
    cursor: pointer;
    opacity: 0.5;
}

.form {
    flex-direction: column;
}

.form__title {
    width: 100%;
    min-height: 23px;
    font-family: 'Inter', 'Arial', sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
    margin: 0;
    padding: 0;
}

.form__user {
    margin: 50px auto 0;
    padding: 0;
    border: 0;
    display: flex;
    flex-direction: column;
}

.form__item {
    width: 100%;
    border: 1px solid rgb(255, 255, 255, .2);
    border-bottom-style: groove;
    font-family: 'Inter', 'Arial', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    margin: 0;
    padding: 0 0 5px;
    color: #000000;
}

.form__item-error {
    font-size: 12px;
    color: red;
    line-height: 1.2;
    font-weight: normal;
    min-height: 30px;
}

.form__item-error_visible {
    visibility: visible;
}

.form__item_invalid {
    border: 1px solid #FF0000;
}

.form__button {
    width: 100%;
    min-height: 50px;
    background: #000000;
    border-radius: 2px;
    font-family: 'Inter', 'Arial', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    color: #FFFFFF;
    padding: 0;
    margin: 22px 0 0;
    border: none;
}

.form__button:hover {
    opacity: 0.8;
    cursor: pointer;
}

.form__button_disabled {
    background-color: white;
    color: black;
    border: 1px solid black;
    opacity: 0.2;
}

.form__button_disabled:hover {
    opacity: 0.2;
    cursor: not-allowed;
}

@media screen and (max-width: 1125px) {
    .author__container {
        align-items: center;
        margin-right: 37px;
        padding-top: 60px;
        padding-bottom: 60px;
        justify-content: space-between;
        flex-direction: column;
        align-items: center;
    }

    .author__avatar {
        margin-bottom: 11px;
        width: 144px;
        height: 144px;
    }

    .author__avatar:hover::after {
        background-size: 33px;
    }

    .author__avatar-image {
        min-height: 144px;
        width: 144px;
    }

    .author__name {
        width: 100%;
        font-size: 30px;
    }

    .author__name-edit {
        width: 70px;
        height: 60px;
    }

    .author__info {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .author__works {
        font-size: 30px;
        margin-left: 0;
    }

    .author__data {
        justify-content: space-evenly;
    }
}


@media screen and (max-width: 883px) {
    .author__icons {
        width: 100%;
    }

    .author__icon-number {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 24px;
    }

}



@media screen and (max-width: 599px) {
    .author__container {
        margin-left: 0;
        margin-right: 0;
    }

    .author__name {
        font-size: 24px;
    }

    .author__name:hover {
        opacity: 1;
    }

    .author__name:hover::after {
        background-color: rgba(155, 147, 147, 0.8);
        background-image: url(../../images/AuthorEdit.svg);
        background-position: 50%;
        background-repeat: no-repeat;
        background-size: 33px;
        border-radius: 16%;
        bottom: 0;
        content: "";
        cursor: pointer;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
        transition: opacity .3s ease-in-out;
    }

    .author__name-edit {
        display: none;
    }

    .author__works {
        width: 100%;
        font-size: 24px;
        justify-content: space-around;
    }

    .tailorImage {
        width: 60px;
        height: 60px;
    }
}